import Vue from 'vue'
import VueRouter from 'vue-router'

import LoginView from '../views/LoginView.vue'
import layoutView from '../views/layoutView.vue'
import homeView from '../views/homeView.vue'
// import NotFoundView from '../views/NotFoundView.vue'
import xitongguanli_routes from './xitongguanli.js' //
import fangchanguanli_routes from './fangchanguanli.js'  //
import zhanghaoguanli_routes from './zhanghaoguanli.js'  //
import feiyongguanli_routes from './feiyongguanli.js' //
import yezhuguanli_routes from './yezhuguanli' //
import notice_routes from './notice' //
import report_routes from './report' //
import qrcode_routes from './qrcode' //+
import weixin_routes from './weixin' //

Vue.use(VueRouter)

const routes = [
  {
    path: '/login',
    name: 'login',
    component: LoginView
  },
  {
    path: '/',
    name: 'layoutView',
    component: layoutView,
    redirect: '/home',
    children:[
      ...[{ path: '/home', name: 'home', component: homeView }],
      // (...)
      ...xitongguanli_routes.children,
      ...fangchanguanli_routes.children,
      ...zhanghaoguanli_routes.children,
      ...feiyongguanli_routes.children,
      ...weixin_routes.children,
      ...yezhuguanli_routes.children,
      ...notice_routes.children,
      ...report_routes.children,
      ...qrcode_routes.children,
      // // 
      // { path: '/:pathMatch(.*)*', name: 'NotFound', component: NotFoundView }
    ]
  },
  // xitongguanli.js/xitongguanlilayoutView
  // xitongguanli_routes,
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

// token
// geocoderapi
router.beforeEach((to, from, next)=>{
  if (to.name !== 'login' && !sessionStorage.token && !to.path.includes('geocoder')) next({ name: 'login' })
  else next()
})

export default router
