<template>
  <div style="height:100vh;display: flex;">
    <v-navigation-drawer :mini-variant="drawer" :expand-on-hover="drawer" app>
      <my-navigationdrawer></my-navigationdrawer>
    </v-navigation-drawer>
    <v-app-bar dense dark color="primary" app>
      <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>
      <v-toolbar-title>{{ title }}</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn text @click="logout"></v-btn>
      <template v-slot:extension>
        <v-tabs>
          <v-tab to="/home">
            
          </v-tab>
          <v-tab v-for="(tab, index) in $store.state.tabs" :key="index" :to="tab.link">
            {{ tab.name }}
            <v-btn @click.prevent="tab_close_btn(index)" class="tab_close" x-small icon>
              <v-icon x-small>mdi-close</v-icon>
            </v-btn>
          </v-tab>
        </v-tabs>
      </template>
    </v-app-bar>

    <v-main>
      <v-container fluid style="height: 100%;" class="d-flex">
        <keep-alive>
          <router-view :key="$route.path"></router-view>
        </keep-alive>
      </v-container>
    </v-main>
  </div>
</template>

<script>
import navigationdrawer from "@/views/components/navigation-drawer.vue";

export default {
  name: "layoutView",
  data: () => ({
    drawer: false,
    title: (sessionStorage.company == 'null'? '':JSON.parse(sessionStorage.company).qiyejiancheng) + ''+sessionStorage.nickname+''
  }),
  methods: {
    tab_close_btn(index) {
      // keepalive
      console.log('keepalive')
      console.log(this.$children[2].$children[0].$vnode.parent.componentInstance.cache)
      console.log('' + this.$store.state.tabs[index].link)
      delete this.$children[2].$children[0].$vnode.parent.componentInstance.cache[this.$store.state.tabs[index].link]
      console.log('keepalive')
      console.log(this.$children[2].$children[0].$vnode.parent.componentInstance.cache)

      // 
      if (this.$store.state.tabs.length == 1 && this.$route.path != '/home') {
        this.$router.push('/')
      } else {
        // 
        if (this.$route.path == this.$store.state.tabs[index].link) {
          var new_index = 0
          // tab
          if (index == this.$store.state.tabs.length - 1) {
            new_index = this.$store.state.tabs.length - 2
          }
          // 
          else {
            new_index = this.$store.state.tabs.length - 1
          }
          var new_link = this.$store.state.tabs[new_index].link
          this.$router.push(new_link)
        }
      }

      // tab
      this.$store.state.tabs.splice(index, 1)

    },

    // 
    get_home_info(){
      
    },

    // 
    logout(){
      sessionStorage.clear()
      this.$store.state.tabs = []
      this.$router.push('/login')
    }
  },
  components: {
    "my-navigationdrawer": navigationdrawer,
  },
  beforeCreate(){
    // token
    if(!sessionStorage.token){
      this.$router.push("/login")
    }
  },
  mounted(){
    this.get_home_info()
  }
};
</script>
<style scoped>
.tab_close {
  position: absolute;
  right: 0px;
  top: 0px;
}
</style>
