<template>
    <v-row>
        <v-col cols="12" sm="3" md="3" height="100%">
            <v-card height="100%" style="overflow: auto;">
                <v-card-title></v-card-title>
                <v-card-text>
                    <v-row>
                        <v-col cols="12" sm="12" md="12">
                            <v-select :items="xiaoqu_items" item-text="name" item-value="_id" v-model="select_xiaoqu"
                                label="" @change="change_xiaoqu"></v-select>
                        </v-col>
                    </v-row>
                    <!--  -->
                    <v-alert type="error" outlined dense v-show="Object.keys(select_xiaoqu).length == 0">
                    </v-alert>
                    <v-treeview :items="loudong_items" item-key="_id" transition :active.sync="active" open-on-click activatable
                        dense>
                    </v-treeview>
                </v-card-text>
            </v-card>
        </v-col>
        <v-col cols="12" sm="9" md="9" height="100%">
            <v-card height="100%" style="overflow: auto;">
                <v-card-title>{{ fangwu_items.length }} 
                    <v-btn class="ml-5" color="success" :disabled="btn_save_all_disabled" small @click="btn_save"> 
                    </v-btn>
                    <v-spacer></v-spacer>
                    <v-btn class="mr-5" color="error" :disabled="select_fangwu.length == 0 ? true : false" small
                        @click="btn_del"> </v-btn>
                    <v-btn class="mr-5" color="primary" small @click="btn_add_single"
                        :disabled="active.length == 0 ? true : false"></v-btn>
                    <v-dialog v-model="dialog" width="500">
                        <template v-slot:activator="{ on, attrs }">
                            <v-btn color="primary" small
                                :disabled="active.length == 0 ? true : false" v-bind="attrs"
                                v-on="on">
                            </v-btn>
                        </template>
                        <v-card>
                            <v-card-title>
                                
                            </v-card-title>

                            <v-card-text>
                                <v-container>
                                    <v-row>
                                        <v-col cols="12" sm="3" md="3">
                                            <v-text-field v-model="editedItem.start_louceng" type="number" label="">
                                            </v-text-field>
                                        </v-col>
                                        <v-col cols="12" sm="3" md="3">
                                            <v-text-field v-model="editedItem.end_louceng" type="number" label="">
                                            </v-text-field>
                                        </v-col>
                                        <v-col cols="12" sm="3" md="3">
                                            <v-text-field v-model="editedItem.start_fanghao" type="number" label="">
                                            </v-text-field>
                                        </v-col>
                                        <v-col cols="12" sm="3" md="3">
                                            <v-text-field v-model="editedItem.end_fanghao" type="number" label="">
                                            </v-text-field>
                                        </v-col>
                                        <v-col cols="12" sm="3" md="3">
                                            <v-text-field v-model="editedItem.biaoshi" label=""></v-text-field>
                                        </v-col>
                                        <v-col cols="12" sm="12" md="12">
                                            <span class="primary--text">{{ count }} </span>
                                        </v-col>
                                    </v-row>
                                </v-container>
                            </v-card-text>

                            <v-divider></v-divider>

                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" text @click="dialog = false">
                                    
                                </v-btn>
                                <v-btn color="blue darken-1" text @click="btn_add_multiple">
                                    
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>
                </v-card-title>
                <v-card-text>
                    <v-data-table v-model="select_fangwu" checkbox-color="error" show-select disable-pagination
                        hide-default-footer height="68vh" :headers="headers" :items="fangwu_items" disable-sort
                        item-key="_id" :loading="loading">
                        <template v-slot:[`item.name`]="{ item }">
                            <v-text-field dense v-model="item.name" color="primary" flat solo-inverted
                                @change="fangwu_change(item)" hide-details="auto"></v-text-field>
                        </template>
                        <template v-slot:[`item.mianji`]="{ item }">
                            <v-text-field dense v-model="item.mianji" color="primary" flat solo-inverted
                                @change="fangwu_change(item)" hide-details="auto" type="number"></v-text-field>
                        </template>
                        <template v-slot:[`item.yezhu`]="{ item }">
                            <v-text-field dense v-model="item.yezhu" color="primary" flat solo-inverted
                                @change="fangwu_change(item)" hide-details="auto"></v-text-field>
                        </template>
                        <template v-slot:[`item.sex`]="{ item }">
                            <v-select dense v-model="item.sex" :items="sex_items" color="primary" flat solo-inverted
                                @change="fangwu_change(item)" hide-details="auto"></v-select>
                        </template>
                        <template v-slot:[`item.phone`]="{ item }">
                            <v-text-field dense v-model="item.phone" color="primary" flat solo-inverted
                                @change="fangwu_change(item)" hide-details="auto"></v-text-field>
                        </template>
                        <template v-slot:[`item._edited`]="{ item }">
                            <v-icon v-if="item._edited" small color="error">mdi-pencil</v-icon>
                            <v-icon v-else small color="success">mdi-check</v-icon>
                        </template>
                    </v-data-table>
                </v-card-text>
            </v-card>
        </v-col>
    </v-row>
</template>
<script>
export default {
    data: () => ({
        active: [],
        // 
        btn_save_all_disabled: true,
        btn_del_disabled: true,
        // 
        select_fangwu: [],
        // 
        sex_items: [{ value: 0, text: '' }, { value: 1, text: '' }, { value: 2, text: '' }],

        // 
        xiaoqu_items: [],
        loudong_items: [],

        // 
        select_xiaoqu: {},
        loading:false,
        headers: [
            { text: '',width: 200, value: 'name' },
            { text: '',width: 150, value: 'mianji' },
            { text: '',width: 200, value: 'yezhu' },
            { text: '',width: 100, value: 'sex' },
            { text: '',width: 200, value: 'phone' },
            { text: '',width: 50, value: '_edited' },
        ],
        fangwu_items: [],

        // 
        dialog: false,
        editedItem: {
            start_louceng: 1,
            end_louceng: 1,
            start_fanghao: 1,
            end_fanghao: 1,
            biaoshi: ''
        },
        count: 1
    }),

    mounted() {
        this.get_xiaoqu_list()
    },

    watch: {
        // 
        fangwu_items: {
            handler: function (val) {
                try {
                    val.forEach((item) => {
                        if (item._edited) {
                            this.btn_save_all_disabled = false
                            throw Error('')
                        } else {
                            this.btn_save_all_disabled = true
                        }
                    })
                } catch (e) {
                    console.log(e)
                }
            },
            deep: true,//
        },
        dialog(val) {
            if (val == false) {
                this.editedItem = {
                    start_louceng: 1,
                    end_louceng: 1,
                    start_fanghao: 1,
                    end_fanghao: 1,
                    biaoshi: ''
                }
            }
        },
        editedItem: {
            handler: function (val) {
                console.log(val)
                var x = Number(val.start_louceng)
                var y = Number(val.end_louceng)
                var a = Number(val.start_fanghao)
                var b = Number(val.end_fanghao)
                this.count = (y - x + 1) * (b - a + 1)
            },
            deep: true
        },
        active(val) {
            if (val.length == 0) {
                this.fangwu_items = []
                this.btn_save_all_disabled = true
            } else {
                this.get_fangwu_list()
            }
        }
    },
    methods: {
        // 
        get_xiaoqu_list() {
            this.$axios.get('/xiaoqu/getlist')
                .then((res) => {
                    console.log(res.data)
                    this.xiaoqu_items = res.data
                })
        },
        // 
        get_loudong_list() {
            this.$axios.get('/loudong/get_loudong_danyuan', { params: { xiaoqu: this.select_xiaoqu } })
                .then((res) => {
                    console.log(res.data)
                    this.loudong_items = res.data
                })
        },

        // 
        get_fangwu_list() {
            this.loading = true
            this.fangwu_items = []
            this.$axios.get('/fangwu/getlist', { params: { danyuan: this.active[0] } })
                .then((res) => {
                    console.log(res.data)
                    // _edited
                    if (res.data) {
                        res.data.forEach((item) => {
                            item._edited = false
                        })
                        setTimeout(() => {
                            this.loading = false
                            this.fangwu_items = res.data
                        }, 1000);
                        
                    }
                })
            this.select_fangwu = []
        },

        // 
        change_xiaoqu() {
            console.log('' + this.select_xiaoqu)
            this.fangwu_items = [] // 
            this.get_loudong_list() // 
        },

        // 
        btn_add_single() {
            this.fangwu_items.push({ name: '', mianji: 0, yezhu: '', sex: 0, phone: '', danyuan: this.active[0], _edited: true })
        },

        // 
        btn_add_multiple() {
            var x = Number(this.editedItem.start_louceng)
            var y = Number(this.editedItem.end_louceng)
            var a = Number(this.editedItem.start_fanghao)
            var b = Number(this.editedItem.end_fanghao)
            console.log(this.count)
            for (x; x <= y; x++) {
                var i = a
                var j = b
                for (i; i <= j; i++) {
                    var name = x + '-' + i + this.editedItem.biaoshi
                    this.fangwu_items.push({ name: name, mianji: 0, yezhu: '', sex: 0, phone: '', danyuan: this.active[0], _edited: true })
                }
            }
            this.dialog = false
        },

        // -------------------------------------------------
        async btn_del() {
            this.fangwu_items = this.fangwu_items.filter((item) => {
                return !this.select_fangwu.includes(item)
            })
            var delete_items = []
            this.select_fangwu.forEach((item) => {
                if (item._id) {
                    delete_items.push(item)
                }
            })
            this.$axios.post('/fangwu/delete', delete_items)
            this.select_fangwu = []
        },

        // ---------------------------------------------------------------
        async btn_save() {
            var update_items = [] // 
            var create_items = [] // 
            this.fangwu_items.forEach((item) => {
                if (item._edited) {
                    if (item._id) {
                        update_items.push(item)
                    } else {
                        create_items.push(item)
                    }
                }
            })
            // 
            const promise1 = this.$axios.post('/fangwu/update', update_items)
            // 
            const promise2 = this.$axios.post('/fangwu/create', create_items)

            await Promise.all([promise1, promise2]).then((res) => {
                console.log(res);
            })
            this.get_fangwu_list()
        },

        // 
        fangwu_change(item) {
            item._edited = true
        },
    }
}
</script>