//==LODOP,:==
function getLodop() {
    var strFontTag = "<font color='#FF00FF'>";
    var strCLodopInstallA = "<font color='#FF00FF'>WebCLodop<a href='/download/lodop/CLodop_Setup_for_Win32NT.exe' target='_self'></a>";
    var strCLodopInstallB = "<br><a href='CLodop.protocol:setup' target='_self'></a>";
    var strCLodopUpdate = "<font color='#FF00FF'>WebCLodop!<a href='/download/lodop/CLodop_Setup_for_Win32NT.exe' target='_self'></a>";

    var strInstallOK = "</font>";
    var LODOP;
    try {
        if (window.getCLodop) {
            LODOP = window.getCLodop();
            if (LODOP.CVERSION < "6.5.6.9") {
                return { error: 1, msg: strFontTag + strCLodopUpdate + strInstallOK };
            } else {
                return LODOP;
            }
        } else {
            return { error: 1, msg: strFontTag + strCLodopInstallA + strCLodopInstallB + strInstallOK };
        }

    } catch (err) {
        alert("getLodop:" + err);
    }
}
export { getLodop }; //getLodop

