import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
        tabs: []
    },
    mutations: {
        // tab
        add(state,new_tab) {
            state.tabs.push(new_tab)
        },
        // tab
        // store.state.tabs
    }
})

export default store