<template>
    <v-row>
        <v-snackbar app top timeout="2000" :color="snackbar.color" v-model="snackbar.show">{{ snackbar.text }}
        </v-snackbar>
        <XiaoquLists @select_xiaoqu="select_xiaoqu"></XiaoquLists>
        <v-col cols="12" sm="9" md="9" style="height:100%">
            <v-card height="100%" style="overflow: auto;">
                <v-card-title>{{ xiaoqu ? xiaoqu.name : '' }} 
                    <v-spacer></v-spacer>
                    <v-dialog v-model="dialog" max-width="500px">
                        <template v-slot:activator="{ on, attrs }">
                            <v-btn color="primary" class="mb-2" v-bind="attrs" v-on="on"
                                :disabled="xiaoqu ? false : true">
                                
                            </v-btn>
                        </template>
                        <v-card>
                            <v-card-title>
                                <span class="text-h5">{{ formTitle }}</span>
                            </v-card-title>

                            <v-card-text>
                                <v-form ref="form">
                                    <v-container fluid>
                                        <v-row>
                                            <v-col cols="12" sm="6" md="6">
                                                <v-select :items="leixing_items" item-text="name" item-value="name"
                                                    v-model="editedItem.leixing" label=""
                                                    :rules="[value => !!value || '']">
                                                    <template v-slot:item="{ item }">
                                                        <v-tooltip right max-width="300px">
                                                            <template v-slot:activator="{ on, attrs }">
                                                                <span v-bind="attrs" v-on="on">{{ item.name }}</span>
                                                            </template>
                                                            <span>{{ item.info }}</span>
                                                        </v-tooltip>
                                                    </template>
                                                </v-select>
                                            </v-col>
                                            <v-col cols="12" sm="6" md="6">
                                                <v-text-field v-model="editedItem.mingcheng" label=""
                                                    :rules="[value => !!value || '']">
                                                </v-text-field>
                                            </v-col>
                                            <v-col cols="12" sm="6" md="6">
                                                <v-text-field v-model="editedItem.danjia" type="number" suffix=""
                                                    label="" :rules="[value => !!value || '']"></v-text-field>
                                            </v-col>
                                            <v-col cols="12" sm="6" md="6">
                                                <v-text-field v-model="editedItem.zhouqi" type="number" suffix=""
                                                    :disabled="disabled_zhouqi" label=""
                                                    :rules="[value => !!value || '', value => !/[\.]/.test(value) || '']">
                                                </v-text-field>
                                            </v-col>
                                        </v-row>
                                    </v-container>
                                </v-form>
                            </v-card-text>

                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" text @click="close">
                                    
                                </v-btn>
                                <v-btn color="blue darken-1" text @click="save">
                                    
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>
                </v-card-title>
                <v-card-text>
                    <v-data-table :headers="headers" :items="items" item-key="_id" :loading="loading" height="66vh">
                        <template v-slot:[`item.leixing`]="{ item }">
                            <v-chip :color="getColor(item.leixing)" dark>
                                {{ item.leixing }}
                            </v-chip>
                        </template>
                        <template v-slot:[`item.zhouqi`]="{ item }">
                            <div>{{ (item.leixing == '' || item.leixing == '') ? '/' : item.zhouqi }}</div>
                        </template>
                        <template v-slot:[`item.status`]="{ item }">
                            <v-switch @change="change_status(item)" v-model="item.status"></v-switch>
                        </template>
                        <template v-slot:[`item.actions`]="{ item }">
                            <v-icon color="primary" small class="mr-2" @click="editItem(item)">
                                mdi-pencil
                            </v-icon>
                            <v-icon color="error" small class="mr-2" @click="deleteItem(item)">
                                mdi-delete
                            </v-icon>
                        </template>
                    </v-data-table>
                </v-card-text>
            </v-card>
        </v-col>
    </v-row>
</template>
<script>
import XiaoquLists from "@/views/components/xiaoqu-lists.vue";
export default {
    components: {
        XiaoquLists
    },
    data: () => ({
        snackbar: {
            show: false,
            text: "",
            color: "success",
            site_error: "",
        },
        dialog: false,
        xiaoqu: null,
        loading:false,
        disabled_zhouqi: false,

        editedIndex: -1,
        editedItem: {
            leixing: '',
            mingcheng: '',
            danjia: 0,
            zhouqi: 0,
        },
        defaultItem: {
            leixing: '',
            mingcheng: '',
            danjia: 0,
            zhouqi: 0,
        },
        // 
        leixing_items: [
            { name: '', info: '' },
            { name: '', info: '' },
            { name: '', info: '' },
            { name: '', info: '' },
            { name: '', info: '' },
        ],
        // 
        items: [],
        headers: [
            { text: "", sortable: true, value: "leixing" },
            { text: "", sortable: false, value: "mingcheng" },
            { text: "", sortable: false, value: "status" },
            { text: "()", sortable: false, value: "danjia" },
            { text: "()", sortable: false, value: "zhouqi" },
            { text: "", sortable: false, value: "actions" },
        ],
    }),
    computed: {
        formTitle() {
            return this.editedIndex === -1 ? '' : ''
        },
    },
    watch: {
        dialog(val) {
            val || this.close()
        },
        editedItem: {
            handler: function (val) {

                if (val.leixing == '' || val.leixing == '') {
                    val.zhouqi = '0'
                    this.disabled_zhouqi = true
                } else {
                    this.disabled_zhouqi = false
                }
            },
            deep: true
        }
    },
    methods: {
        //  
        select_xiaoqu(xiaoqu) {
            this.xiaoqu = xiaoqu
            if (xiaoqu) {
                this.getlist()
            } else {
                this.items = []
            }
        },

        editItem(item) {
            this.editedIndex = this.items.indexOf(item)
            this.editedItem = Object.assign({}, item)
            this.dialog = true
        },
        deleteItem(item) {
            this.$axios.post('/feiyong/del', item)
                .then((res) => {
                    console.log(res)
                    this.close()
                    this.snackbar = {
                        show: true,
                        text: res.data.message,
                        color: res.data.error ? "error" : "success",
                    }
                })
                .catch(error => {
                    console.log(error)
                    this.snackbar = {
                        show: true,
                        text: error,
                        color: "error"
                    }
                })
        },
        close() {
            this.dialog = false
            this.$refs.form.resetValidation()
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem)
                this.editedIndex = -1
            })
        },
        // 
        getlist() {
            this.loading = true
            this.items = []
            this.$axios.get('/feixiang/lists', { params: { xiaoqu: this.xiaoqu._id }})
                .then((res) => {
                    setTimeout(() => {
                        this.loading = false
                        this.items = res.data
                    }, 1000);
                })
        },

        // 
        change_status(item) {
            this.$axios.post('/feixiang/update', item)
                .then((res) => {
                    this.snackbar = {
                        show: true,
                        text: res.data.message,
                        color: res.data.errors ? "error" : "success",
                    }
                })
                .catch(error => {
                    console.log(error)
                    this.snackbar = {
                        show: true,
                        text: error,
                        color: "error"
                    }
                })
        },
        // ---------------------------------------------------------------
        save() {
            if (this.$refs.form.validate()) {
                if (this.editedIndex > -1) {
                    // 
                    this.$axios.post('/feixiang/update', this.editedItem)
                        .then((res) => {
                            console.log(res)
                            this.close()
                            this.getlist()
                            this.snackbar = {
                                show: true,
                                text: res.data.message,
                                color: res.data.errors ? "error" : "success",
                            }
                        })
                        .catch(error => {
                            console.log(error)
                            this.snackbar = {
                                show: true,
                                text: error,
                                color: "error"
                            }
                        })
                } else {
                    // 
                    this.editedItem.xiaoqu = this.xiaoqu
                    console.log(this.editedItem)
                    this.$axios.post('/feixiang/create', this.editedItem)
                        .then((res) => {
                            console.log(res)
                            this.close()
                            this.getlist()
                            this.snackbar = {
                                show: true,
                                text: res.data.message,
                                color: res.data.errors ? "error" : "success",
                            }
                        })
                        .catch(error => {
                            console.log(error)
                            this.snackbar = {
                                show: true,
                                text: error,
                                color: "error"
                            }
                        })
                }
            }
        },
        getColor(leixing) {
            if (leixing == '') return 'primary'
            else if (leixing == '') return 'orange'
            else if (leixing == '') return 'green'
            else if (leixing == '') return 'red'
            else return 'grey'
        },
    },
};
</script>
